/* CSS Document */

/*reset css*/

/** Set default font family and font color and background color */

html {
  font-family: '\5B8B\4F53',arial, verdana, sans-serif;
  color: #333;
  background: #ffffff;
}

/***** body *****/

body {
  font-size: 100%;
  background: #ffffff;
}

/*****Remove default margin and padding*****/

body,
dl,
h1,
h2,
h3,
h4,
h5,
h6,
form,
p {
  margin: 0;
}

input,
button,
textarea,
td,
th {
  padding: 0;
}

menu,
ul,
ol {
  margin: 0;
  padding: 0;
}

/***** Remove default vertical scrollbar in IE 8/9/10/11 and resize button*****/

textarea {
  resize: none;
  overflow: auto;
}

/***** Improve link behaviour*****/

a {
  text-decoration: none;
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

/***** Prevent `sub` and `sup` affecting `line-height` in all browsers. *****/

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

/***** fix border problem *****/

iframe,
img {
  border: 0;
}

/***** remove list style of ul and ol*****/

ul,
ol {
  list-style: none;
}

/***** input and button *****/

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

button:-moz-focusring,
input:-moz-focusring {
  outline: 0.013333rem dotted ButtonText;
}

input {
  line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

input {
  outline: none;
}

input::-webkit-input-placeholder {
  color: #C0C0C0;
}

input::-moz-placeholder {
  color: #C0C0C0;
}

/* Mozilla Firefox 19+ */

input:-moz-placeholder {
  color: #C0C0C0;
}

/* Mozilla Firefox 4 to 18 */

input:-ms-input-placeholder {
  color: #C0C0C0;
}

/* Internet Explorer 10-11 */

/***** table *****/

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*****display behaviour *****/

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

/*****common classes *****/

.clear {
  clear: both;
}

/* 去掉半透明灰色遮罩 */

* {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  tap-highlight-color: rgba(0,0,0,0);
}

/* html{touch-action: none;} */

input,
button,
select,
textarea {
  outline: none;
}